import React, { Fragment, useEffect, useState } from 'react';
import { Card, Input, Table, Button, Modal, Form, message, Tooltip, Popconfirm } from 'antd';
import { PlusOutlined, DeleteOutlined } from '@ant-design/icons';
import { getTypeList, addType, deleteType } from '../../api/api';
import moment from 'moment/moment';

function TypeList() {
  const [form] = Form.useForm();
  const [visible, updateVisible] = useState(false);
  const [data, updateData] = useState([]);

  useEffect(() => {
    getList();
  }, []);

  const columns = [
    // { title: '类别编号', dataIndex: 'id' },
    { title: '类别名称', dataIndex: 'name' },
    { title: '类别名称', key: 'time', render: (_, record) => moment(record.create_time).format('YYYY-MM-DD HH:mm:ss') },
    {
      title: '操作',
      key: 'action',
      width: '120px',
      render: (_, record) => (
        <Popconfirm title="确定删除吗?" onConfirm={() => deleteItem(record)} okText="确定" cancelText="取消">
          <a style={{ color: 'red', cursorJ: 'pointer' }}>
            <DeleteOutlined /> 删除
          </a>
        </Popconfirm>
      ),
    },
  ];

  const getList = (value) => {
    getTypeList({ name: value }).then((res) => {
      if (res?.code === 200) {
        updateData(res?.result);
      }
    });
  };

  const deleteItem = async (item) => {
    const res = await deleteType({ id: item.id });
    if (res?.code === 200) {
      message.success('删除成功');
      getList();
    } else {
      message.error('删除失败');
    }
  };

  const toggleVisible = () => {
    if (visible) {
      form.resetFields();
    }
    updateVisible(!visible);
  };

  const onSearch = (value) => {
    getList(value);
  };

  const handleOk = async () => {
    const values = await form.validateFields();
    const res = await addType(values);
    if (res?.code === 200) {
      message.success('添加成功');
      getList();
      toggleVisible();
    } else {
      message.error('添加失败');
    }
  };

  return (
    <Fragment>
      <Card bordered={false}>
        <div style={{ marginBottom: 10 }}>
          <Input.Search placeholder="输入类别名称搜索" style={{ width: 300 }} allowClear onSearch={onSearch} />
          <Button type="primary" style={{ float: 'right' }} icon={<PlusOutlined />} onClick={toggleVisible}>
            添加
          </Button>
        </div>

        <Table dataSource={data} rowKey="id" columns={columns} pagination={{}} bordered size="small" />
      </Card>

      {visible && (
        <Modal title="添加类别" open cancelText="取消" okText="确定" onOk={handleOk} onCancel={toggleVisible}>
          <Form form={form} labelCol={{ span: 4 }} wrapperCol={{ span: 19 }} initialValues={{}} autoComplete="off">
            <Form.Item label="类别名称" name="name" rules={[{ required: true, message: '请填写类别名称' }]}>
              <Input />
            </Form.Item>
          </Form>
        </Modal>
      )}
    </Fragment>
  );
}

export default TypeList;
